:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

.link-wrap-first {
  max-width: 1000px;
  justify-content: flex-end;
  gap: 240px;
  margin: auto auto 200px;
  padding-top: 30px;
  display: flex;
}

.btn-wrapper {
  max-width: 500px;
  flex-direction: column;
  gap: 50px;
  margin: auto;
  display: flex;
}

button {
  cursor: pointer;
  min-width: 100%;
  min-height: 60px;
  color: #11121c;
  transition-property: background-color, color, box-shadow;
  transition-duration: var(--time-duration);
  transition-timing-function: var(--timing-function);
  background: #eaeaea;
  border: 2px solid rgba(0, 0, 0, 0);
  border-radius: 4px;
  margin: auto;
  padding: 8px 12px;
  font-size: 48px;
  display: block;
  box-shadow: 0 2px 6px rgba(46, 47, 66, .08), 0 2px 2px rgba(46, 47, 66, .16), 0 2px 3px rgba(46, 47, 66, .08);
}

button:-webkit-any(:focus, :hover) {
  color: #fff;
  background-color: rgba(54, 55, 55, .94);
  box-shadow: 0 2px 6px rgba(46, 47, 66, .08), 0 2px 2px rgba(46, 47, 66, .16), 0 2px 3px rgba(46, 47, 66, .08);
}

button:is(:focus, :hover) {
  color: #fff;
  background-color: rgba(54, 55, 55, .94);
  box-shadow: 0 2px 6px rgba(46, 47, 66, .08), 0 2px 2px rgba(46, 47, 66, .16), 0 2px 3px rgba(46, 47, 66, .08);
}

button:active {
  color: #e4e059;
  background-color: rgba(26, 26, 26, .94);
  box-shadow: 0 2px 6px rgba(46, 47, 66, .08), 0 2px 2px rgba(46, 47, 66, .16), 0 2px 3px rgba(46, 47, 66, .08);
}

.isNotActive {
  color: #ababa9;
  background-color: rgba(98, 97, 97, .94);
  box-shadow: 0 2px 6px rgba(46, 47, 66, .08), 0 2px 2px rgba(46, 47, 66, .16), 0 2px 3px rgba(46, 47, 66, .08);
}

